.header {
    background: #fff;
    box-shadow: 0 0 5px rgba( 0,0,0,.3 );
    padding: 20px 15px;
    margin-bottom: 30px;
    &__wrapper {
        display: flex;
        align-items: center;
    }
}

.logo {
    flex: 1;
}

.navigation {
    &__list {
        display: flex;
        &__item {
            font-size: 1rem;
            padding-left: 15px;
            border-left: solid 1px #9E9E9E;
            margin-left: 15px;
            &:first-child {
                border-left: none;
                margin-left: 0;
            }
            &__link {
                text-decoration: none;
                color: #525252;
                cursor: pointer;
                &:hover {
                    color: #303f9f;
                }
            }

        }
    }
}